import Vue from 'vue'
import Router from 'vue-router'
import VueLazyload from 'vue-lazyload'
import Hello from '@/components/child/Hello'
import first from '@/components/child/first'
import second from '@/components/child/second'
import Main from '@/components/Main/Main'
import starFlow from '@/components/Main/starFlow'


Vue.use(VueLazyload, {
  preLoad: 1.3,
  // error: require('../assets/coding.jpg'),
  loading: 'http://cdn.uehtml.com/201402/1392662591495_1140x0.gif',
  attempt: 6,
  listenEvents: [ 'scroll', 'mousewheel' ]
});

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      children:[
        {  path: '/first', component: first},
        {  path: '/Hello', component: Hello},
        {  path: '/second', component: second}
        ]
    },
    {
      path: '/starFlow',
      name: 'starFlow',
      component: starFlow
    }
  ]
})





