<template>
  <div class="first">
    <div class="firstImg">
      <div class="firstImgChild">
        <ul>
          <li v-for="item in headPic"><img v-lazy="item.src" /></li>
        </ul>
      </div>
    </div>
    <div class="personalInfo">
      <h3>{{msg}}</h3>
    </div>
    <div class="workPic">
      <div class="allThePicContainer">
      <div class="whichTd">
        <a>
          <div class="workPicChild" v-for="item in picList">
            <div><img v-lazy="item.src" /><div class="imgDesinfo"><div>{{item.des}}</div></div></div>
          </div>
        </a>
      </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'first',
    data () {
      return{
        msg:'我是刘小四,一名普通的前端开发工程师,我的梦想是世界和平。',
        headPic :[
          {src:'static/img/lufei-3.jpeg'},
          {src:'static/img/lufei-2.jpg'},
          {src:'static/img/lufei-1.jpg'}
        ],
        picList: [
          {src:'static/img/coding.jpg',des:'我是键盘侠,键盘'},
          {src:'static/img/coding.jpg',des:'我的吉他,吉他'},
          {src:'static/img/coding.jpg',des:'那些山山水水,旅行'},
          {src:'static/img/coding.jpg',des:'我的音乐'},
          {src:'static/img/coding.jpg',des:'随手拍'},
          {src:'static/img/coding.jpg',des:'我'},


        ]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  @keyframes headPicMove{
       0% {top:0;}
       50% {top:-250px;}
       100% {top:-500px;}
  }
  h1, h2 {
    font-weight: normal;
  }

  ul {
    /*list-style-type: none;*/
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
  .first {
    width: 1100px;
    height: 100%;
    margin: 0 auto;
    .firstImgChild{
      width: 33.3%;
      height: 270px;
      overflow: hidden;
      margin: 0 auto;
      ul{
        position: relative;
        animation:headPicMove 10s infinite;
      }
    }
    .personalInfo{
      margin-top:40px;
    }
   .firstImg img{
     width: 250px;
     border-radius: 100%;
     padding-top: 40px;
   }
  }
  .workPic{
    width: 100%;
    margin-top: 100px;
    display: table;
    .allThePicContainer{
      width: 90%;
      margin:0 auto;
    }
    .workPicChild{
      width: 33.3333333333%;
      float: left;
      padding: 10px 0;
      position: relative;
      min-height: 1px;
    }
    .imgDesinfo{
      height: 0;
      position: absolute;
      width: 100%;
      top: 10px;
      overflow: hidden;
      div{
        width: 85%;
        margin: 0 auto;
        background: #fff;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        z-index: 100;
        opacity: 1;
      }
    }
    .workPicChild:hover .imgDesinfo{
      transition: height .5s ;
      height: 30px;
    }
    .imgChild{
      width: 30%;
      float: left;
    }
    img{
      opacity: 0.5;
      border-radius: 10px;
      width: 85%;
    }
    img:hover{
      transition: opacity .5s;
      opacity: 1;
    }
  }


</style>



